/* 네비바 하단 고정 밑 세로 길이 설정 */
.wrapper {
    position: fixed;
    bottom: 0;
    left: 0;
    right: 0;
    /* width: 390px; */
    height: 45px;
    z-index: 9999;
  }
  
  /* nav태그 아래의 자식들을 수평정렬하기 위한 설정 */
  nav {
    z-index: 9999;
    overflow: hidden;
    background-color: white;
    /* border-top: 2px solid grey; */
  }
  /* nav태그 아래의 div태그들을 수평정렬 및 세로길이 설정 */
  .nav-link {
    /* 수평정렬, 5개의 button을 각각 20% width만큼 할당 */
    float: left;
    width: 25%;
    text-align: center;
  
    /* 세로길이 설정 */
    height: 45px;
    line-height: 45px;
  }
  
  /* 하단 네비바의 메뉴를 눌렀을 때 색상 변경*/
  .nav-item {
    color: grey;
  }
  
  /* 하단 네비바의 메뉴를 눌렀을 때 색상 변경*/
  .active {
    color: #FF9C28;
  }